<template>
  <div>
    <div class="list-group text-center">
      <router-link to="/products" class="list-group-item list-group-item-action" :class="{'active': choose === ''}" @click.native="chooseCategory"></router-link>
      <router-link :to="{path: '/products', query: {category: ''}}" class="list-group-item list-group-item-action" :class="{'active': choose === ''}" @click.native="chooseCategory"></router-link>
      <router-link :to="{path: '/products', query: {category: ''}}" class="list-group-item list-group-item-action" :class="{'active': choose === ''}" @click.native="chooseCategory"></router-link>
      <router-link :to="{path: '/products', query: {category: ''}}" class="list-group-item list-group-item-action" :class="{'active': choose === ''}" @click.native="chooseCategory"></router-link>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      choose: ''
    }
  },
  methods:{
    chooseCategory(e){
      this.choose = e.target.text; 
      this.$emit('changeCategory');      
    }
  },
  created() {
    if(this.$route.query.category){
      this.choose = this.$route.query.category;
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


