<template>
  <div>
    <div class="swiper-container">
      <div class="swiper-wrapper">
          <div class="swiper-slide banner1 bg-cover"></div>
          <div class="swiper-slide banner2 bg-cover"></div>
          <div class="swiper-slide banner3 bg-cover"></div>
      </div>
      <div class="swiper-pagination"></div>   
    </div>

    <div class="container-fluid">
      <div class="row justify-content-center ">
        <div class="col-md-9 my-md-4 my-3">
          <div class="form-row">
            <div class="col-4 d-flex justify-content-center align-items-center">
              <router-link :to="{path: '/products', query: {category: ''}}" class="btn btn-lg text-white text-center btn-block py-md-6 py-5 bg-cover menu-sashimi border-0 shadow">
                <b></b>
              </router-link>
            </div>
            <div class="col-4 d-flex justify-content-center align-items-center">
              <router-link :to="{path: '/products', query: {category: ''}}" @click.native="changeNavbarActive" class="btn btn-lg text-white text-center btn-block py-md-6 py-5 bg-cover menu-sushi border-0 shadow">
                <b></b>
              </router-link>
            </div>
            <div class="col-4 d-flex justify-content-center align-items-center">
              <router-link :to="{path: '/products', query: {category: ''}}" @click.native="changeNavbarActive" class="btn btn-lg text-white text-center btn-block py-md-6 py-5 bg-cover menu-ton border-0 shadow">
                <b></b>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <section class="container-fluid text-white p-0" style="position: relative;">
      <div class="container">
        <div class="row justify-content-center justify-content-md-start">
          <div class="col-md-4 bg-cover introduction-image chef p-0"></div>
        </div>
      </div>
      <div class="row no-gutters">
        <div class="container-fluid bg-desktop my-4">
          <div class="container">
            <div class="row justify-content-end">
              <div class="col-md-6 text-md-title m-3">
                <h3 class="text-center mt-3 pb-3 border-md-title"></h3>
                <ul class="list-unstyled">
                  <li class="mt-4">
                    <h4></h4>
                    <p class="text-md-content"></p>
                  </li>
                  <li class="mt-4">
                    <h4></h4>
                    <p class="text-md-content"></p>
                  </li>
                  <li class="mt-4">
                    <h4></h4>
                    <p class="text-md-content"></p>
                  </li>
                </ul>
              </div>
            </div> 
          </div>
        </div> 
      </div>      
    </section>

    <section class="container-fluid mt-md-5 p-0 mb-md-4" style="position: relative;">
      <div class="container">
        <div class="row justify-content-center justify-content-md-end">
          <div class="col-md-4 bg-cover introduction-image freshfood d-none d-md-block"></div>
        </div>  
      </div>  
      <div class="row no-gutters">
        <div class="container-fluid bg-desktop my-md-4">
          <div class="container">
            <div class="row">
              <div class="col-md-6 my-3 align-items-center">
                <h3 class="text-center mt-3 pb-3 border-bottom border-title"></h3>
                <div class="d-flex mt-4 ">
                  <div class="mr-4 d-none d-sm-block">
                    <i class="fas fa-fish fa-4x"></i>
                  </div>
                  <div>
                    <h5 class="h4"></h5>
                    <p class="text-content"></p>
                  </div>
                </div>
                <div class="d-flex mt-4 mb-3">
                  <div class="mr-4 d-none d-sm-block">
                    <i class="fas fa-cookie-bite fa-4x"></i>
                  </div>
                  <div>
                    <h5 class="h4"></h5>
                    <p class="text-content"></p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <img class="d-md-none img-fluid" src="../../assets/images/freshfood.jpg" alt="">  
    </section>
  </div>
</template>

<script>
import Swiper from 'swiper/js/swiper.min.js';
import 'swiper/css/swiper.min.css';

export default {
  data(){
    return {
      
    }
  }, 
  methods: {
    changeNavbarActive(){
      this.$emit('sendChoose');
    },
  },
  mounted() {
    var mySwiper = new Swiper ('.swiper-container', {
      direction: 'horizontal',
      loop: true,
      autoplay: {
        delay: 5000
      },
      pagination: {
        el: '.swiper-pagination',
        clickable: true,
      },
    })
  },
  created() {
    this.$emit('sendRoute', this.$route.name);
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
$title: #3F5D45;
$content: #8DA291;
.swiper-container {
    width: 100%;
    height: 400px;
}
.banner1{
  background-image: url('../../assets/images/banner1.jpg');
}
.banner2{
  background-image: url('../../assets/images/banner2.jpg');
}
.banner3{
  background-image: url('../../assets/images/banner3.jpg');
}
.menu-ton{
  background-image: url('../../assets/images/menu-ton.jpg');
}
.menu-sushi{
  background-image: url('../../assets/images/menu-sushi.jpg');
}
.menu-sashimi{
  background-image: url('../../assets/images/menu-sashimi.jpg');
}
.menu-ton, .menu-sushi, .menu-sashimi{ 
  transition: all 0.2s;
}
.menu-ton:hover, .menu-sushi:hover, .menu-sashimi:hover{
  transform: scale(0.95);
}

.introduction-image{
  position: absolute;
  top: 0;
  bottom: 0;
}
.chef{
  background-image: url('../../assets/images/chef.jpg');
}
.freshfood{
  background-image: url('../../assets/images/freshfood.jpg');
}
.text-md-title{
  background-color: rgba(0, 0, 0, .3);
}
.border-md-title{
  border-bottom: 1px solid white;
}
@media (min-width: 768px) {
  .text-md-title{
    color: $title;
    background-color: transparent;
  }
  .text-md-content{
    color: $content;
  }
  .border-md-title{
    border-bottom: 1px solid $title;
  }
}



</style>



